export const getOffersByType = (offersatalog, type) => offersatalog.find((offer) => offer.type.toLowerCase() === type.toLowerCase());

export const getOferrsCost = (offersatalog, selectedOffers, type) => {
  const availableOffers = getOffersByType(offersatalog, type)?.offers || [];
  const eventOferrsCost = availableOffers.reduce((acc, cur) => {
    if (selectedOffers.includes(cur.id)) {
      acc += cur.price;}
    return acc;
  }, 0);

  return eventOferrsCost;
};
